export const fillPatternSvg = (
  symbolizer: MarkSymbolizer
) => {
  const { wellKnownName, color, fillOpacity, strokeColor, strokeOpacity, strokeWidth } = symbolizer;

  const cleanWellKnownName = wellKnownName.replace('brush://', '');
  const isSimplePatternFill = !strokeWidth && NOFILL_WELLKNOWNNAMES.includes(String(cleanWellKnownName));
  const isDenseFill = cleanWellKnownName.startsWith('dense');
  let svgPattern;

  if (isDenseFill || isSimplePatternFill) {
    svgPattern = fillPatterns[cleanWellKnownName];
    if (isDenseFill) {
      svgPattern = svgPattern.replace(/fill:black/g, `fill:${color}`);
      svgPattern = svgPattern.replace(/fill-opacity:1/g, `fill-opacity:${fillOpacity ?? 1}`);
    } else {
      // Special rules for simple fills that only have a stroke color, but in this case need a fill color
      svgPattern = svgPattern.replace(/fill:black/g, `fill:${strokeColor}`);
      svgPattern = svgPattern.replace(/fill-opacity:1/g, `fill-opacity:${strokeOpacity ?? 1}`);
    };
  } else {
    svgPattern = getPointSvg(symbolizer);
  };

  return svgPattern;
};
